import React, { useState } from "react";
import { Box, HStack, useBreakpointValue, VStack } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import ShadowButton from "../../../components/ShadowButton";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import * as pipelineActions from "../../../store/actions/pipelines";
import { vh, vw } from "../../../util";

const AddPipeline = ({ setShowModal, showModal, _showAlert }) => {
  const dispatch = useDispatch();
  const [isSaving, setIsSaving] = useState(false);

  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "all",
  });

  const onSubmit = async (data) => {
    // const name = nameRef.current.value;

    if (!data.name) {
      _showAlert("error", "Pipeline name is required.");
      return;
    }
    setIsSaving(true);
    try {
      const action = pipelineActions.addPipeline(data, null);
      await dispatch(action);
      reset();
      setIsSaving(false);
      setShowModal(false);
      _showAlert("success", "Pipeline created successfully.");
    } catch (err) {
      console.log(err.message);
      _showAlert("error", "An error occured, cannot create pipeline.");
      setIsSaving(false);
    }
  };

  return (
    <ShadowModal
      size='md'
      height={vh(250)}
      bodyHeight={vh(80)}
      // width={vw(400)}
      isOpen={showModal}
      close={() => {
        reset();
        setShowModal(false);
      }}
      cancel={() => {
        reset();
        setShowModal(false);
      }}
      save={handleSubmit(onSubmit)}
      header='Add Pipeline'
      buttons={
        <HStack space={2}>
          <ShadowButton
            space={2}
            onPress={() => {
              reset();
              setShowModal(false);
            }}
            color='rgba(0,0,0, 0.5)'
            variant='outline'
            label='Cancel'
            style={{ height: vh(36) }}
          />
          <ShadowButton
          space={3}
            isLoading={isSaving}
            onPress={handleSubmit(onSubmit)}
            color='white'
            label='Save'
            style={{ height: vh(36) }}
          />
        </HStack>
      }
    >
      <VStack marginTop={vh(-20)}>
        <ShadowInput
          autoFocus={true}
          style={{ width: "100%" }}
          onKeyPress={(e) => {
            if (e.key === "Tab" || e.key === "Enter") {
              e.preventDefault();
              setIsSaving(true);
              handleSubmit(onSubmit, () => {
                setIsSaving(false);
              })();
            }
          }}
          name='name'
          label={"Pipeline Name"}
          placeholder='Pipeline Name'
          control={control}
          rules={{
            required: {
              value: true,
              message: "Please enter pipeline name",
            },
          }}
          invalid={errors.name ? true : false}
          error={errors.name ? errors.name.message : ""}
          inputStyle={{}}
        />
      </VStack>
    </ShadowModal>
  );
};

export default AddPipeline;
