import React, { useState, useEffect } from "react";
import { HStack, Pressable, VStack, Text, Checkbox } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";

import ShadowButton from "../../../components/ShadowButton";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import * as listActions from "../../../store/actions/lists";
import * as campaignActions from "../../../store/actions/campaigns";
import { vh } from "../../../util";
import ShadowSelect from "../../../components/ShadowSelect";
import Colors from "../../../constants/Colors";

const AddList = ({ setShowModal, showModal, _showAlert, edit }) => {
  const dispatch = useDispatch();
  const [isSaving, setIsSaving] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const { activeList: list, lists } = useSelector((state) => state.lists);
  const { userCampaigns: campaigns } = useSelector((state) => state.campaigns);
  const listname = list && list.name;

  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    getValues,
  } = useForm({
    mode: "all",
  });

  useEffect(() => {
    (async () => {
      try {
        await dispatch(campaignActions.fetchCampaigns(1, 1000));
      } catch (err) {
        _showAlert("error", err.message);
      }
    })();
  }, [dispatch]);

  useEffect(() => {
    reset();
    if (edit) {
      setValue("_id", list ? list._id : null);
      setValue("name", list ? list.name : "");
      setValue("campaign", list ? list.campaign : "");
      setValue("applyto", list ? list.applyto : "all");
      setValue("exclude", list ? list.exclude : false);
    } else {
      setValue("_id", null);
      setValue("name", "");
      setValue("campaign", "");
      setValue("applyto", "all");
      setValue("excclude", false);
    }
  }, [listname]);

  const onSubmit = async (data) => {
    if (!data.name) {
      _showAlert("error", "List name is required.");
      return;
    }
    if (data.campaign === "") {
      delete data.campaign;
    }
    setIsSaving(true);
    try {
      if (edit) {
        data._id = list._id;
      }
      const action = listActions.addList(data, null);
      await dispatch(action);
      await dispatch(listActions.fetchLists());
      reset();
      setIsSaving(false);
      setShowModal(false);
      _showAlert("success", "List created successfully.");
    } catch (err) {
      console.log(err.message);
      _showAlert("error", "An error occured, cannot create list.");
      setIsSaving(false);
    }
  };

  const campaignsSelected = lists.map((l) => l.campaign);
  const campaignsFiltered = edit
    ? campaigns.filter((c) => {
        if (c._id === list && list.campaign) {
          return true;
        }
        return !campaignsSelected.includes(c._id);
      })
    : campaigns.filter((c) => !campaignsSelected.includes(c._id));

  return (
    <ShadowModal
      size='lg'
      height={vh(300)}
      bodyHeight={vh(250)}
      style={{
        minHeight: vh(300),
      }}
      // width={vw(400)}
      isOpen={showModal}
      close={() => {
        reset();
        setShowModal(false);
      }}
      cancel={() => {
        reset();
        setShowModal(false);
      }}
      save={handleSubmit(onSubmit)}
      header={edit ? "Edit List - " + listname : "Add List"}
      buttons={
        <HStack space={2}>
          <ShadowButton
            space={2}
            onPress={() => {
              reset();
              setShowModal(false);
            }}
            color='rgba(0,0,0, 0.5)'
            variant='outline'
            label='Cancel'
            style={{ height: vh(36) }}
          />
          <ShadowButton
            space={3}
            isLoading={isSaving}
            onPress={handleSubmit(onSubmit)}
            color='white'
            label='Save'
            style={{ height: vh(36) }}
          />
        </HStack>
      }
    >
      <VStack space={5} marginTop={vh(-20)}>
        <ShadowInput
          autoFocus={true}
          style={{ width: "100%" }}
          // onKeyPress={(e) => {
          //   if (e.key === "Tab" || e.key === "Enter") {
          //     e.preventDefault();
          //     setIsSaving(true);
          //     handleSubmit(onSubmit, () => {
          //       setIsSaving(false);
          //     })();
          //   }
          // }}
          name='name'
          label={"List Name"}
          placeholder='List Name'
          control={control}
          rules={{
            required: {
              value: true,
              message: "Please enter list name",
            },
          }}
          invalid={errors.name ? true : false}
          error={errors.name ? errors.name.message : ""}
          inputStyle={{}}
        />
        <ShadowSelect
          control={control}
          items={[
            { label: "< Set Later >", value: "" },
            ...campaignsFiltered.map((c) => ({
              label: c.name,
              value: c._id,
            })),
          ]}
          name='campaign'
          label='Attach Campaign'
          placeholder='Select Campaign'
          invalid={errors.campaign ? true : false}
          error={errors.campaign ? errors.campaign.message : ""}
          rules={{
            required: {
              value: false,
              message: "Select campaign",
            },
          }}
        />
        <ShadowSelect
          control={control}
          items={[
            { label: "All", value: "all" },
            { label: "New", value: "new" },
            { label: "Selected", value: "selected" },
          ]}
          name='applyto'
          label='Apply To'
          placeholder='Select'
          invalid={errors.applyto ? true : false}
          error={errors.applyto ? errors.applyto.message : ""}
          rules={{
            required: {
              value: false,
              message: "Select applyto",
            },
          }}
          onChange={(v) => {
            if (v !== "all") {
              setValue("exclude", false);
            }
            setRefresh((r) => !r);
          }}
        />
        {getValues("applyto") === "all" && (
          <Checkbox
            size={"sm"}
            onChange={(v) => {
              setValue("exclude", v);
            }}
          >
            <Text style={{ fontFamily: "Poppins", fontSize: 14 }}>
              Exclude properties with campaign completed
            </Text>
          </Checkbox>
        )}
      </VStack>
    </ShadowModal>
  );
};

export default AddList;
