// import _ from "lodash";
import { API_URL, getUser } from "../../constants/Config";
import * as events from "../events";

export const fetchPropertiesInZip = (zip) => {
  return async (dispatch, getState) => {
    const { token } = getUser();
    // console.log(`${API_URL}/properties/inzip/${zip}`);
    try {
      const response = await fetch(`${API_URL}/properties/inzip/${zip}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
      });

      if (!response.ok) {
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();
      // console.log("Total", resData.data.length);

      if (resData.data) {
        dispatch({
          type: events.MAP_PROPERTIES,
          data: resData.data,
        });
        return resData.data;
      }
    } catch (err) {
      // send to custom analytics server
      console.log("fetchPropertiesInZip Error ", err);
      throw err;
    }
  };
};

