const markerIcon = (n, f, mkt = false) => {
  let w = 24;
  w = 20 + 4 * n.toString().length;
  const fill = f || "%23e7e7e7";
  n = n.toString().replace("#", "%23 ");
  var m = {
    url: `data:image/svg+xml;utf-8, <svg id="svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewbox="0 0 24 48" class="svg-rect" width="${
      w + 30
    }" height="24">
    <rect stroke="white" stroke-width="1px" id="rect" x="0" y="0" rx="10" ry="10" width="${w}" height="24" fill="${fill}"></rect>
    <text id="num" font-family="Sanserif" fill="white" font-size="11" x="25%" y="50%" text-anchor="middle" stroke="rgba(255,255,255,1)" stroke-width="1px" dy=".3em">${n}</text>
    <circle id="1" fill="red" cx="${w}" cy="6" r="6" />
    <text x="${w}" y="6" 
          text-anchor="middle"
          stroke="white"
          stroke-width="1px"
          alignment-baseline="middle"
          font-family="Sanserif"
          font-size="8"
          >$
    </text>
    </svg>`,
  };
  if (!mkt) {
    m = {
      url: `data:image/svg+xml;utf-8, <svg id="svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewbox="0 0 24 24" class="svg-rect" width="${w}" height="24">
    <rect stroke="white" stroke-width="1px" id="rect" x="0" y="0" rx="10" ry="10" width="${w}" height="24" fill="${fill}"></rect>
    <text id="num" font-family="Sanserif" fill="white" font-size="11" x="50%" y="50%" text-anchor="middle" stroke="rgba(255,255,255,1)" stroke-width="1px" dy=".3em">${n}</text>
    
    </svg>`,
    };
  }

  return m;
};

